@import '../../global.scss';

.works{
    background-color: $secondaryColor;
    color: $mainColor;
    border-top: 1px solid $mainColor;
    display: flex;

    .title{
        flex: 0.3;
        padding: 5vh 0 5vh 5vw;
    }
    

    .projects{
        padding: 10vh 10vw;
        height: 100%;
        flex: 0.7;
        display: flex;
        flex-direction: column;
        .project{

            a{
                text-decoration: none;
                color: inherit;

                h2{
                    font-size: 30px;
                    border-bottom: 1px solid $mainColor;
                    margin-bottom: 1vh;
                }
            }

            img{
                display: none;
            }
        }
    }
}